import { OnInit, OnDestroy, Directive } from '@angular/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { startWith, pairwise, map, share, debounceTime } from 'rxjs/operators';
import { RouterOutlet } from '@angular/router';

@Directive()
export abstract class RouterSlideAnimation implements OnInit, OnDestroy {
  readonly subs = new Subscription();
  itemChanges$ = new BehaviorSubject(0);
  routerTrigger$ = this.itemChanges$.pipe(
    startWith(this.itemChanges$.getValue()),
    pairwise(),
    map(([prev, next]) => {
      const params = {
        offsetEnterX: 0,
        offsetLeaveX: 0,
      };
      if (prev !== 0) {
        params.offsetEnterX = prev > next ? -100 : 100;
        params.offsetLeaveX = prev > next ? 100 : -100;
      }
      return {
        value: next,
        params,
      };
    }),
    share()
  );
  constructor() {}

  ngOnInit() {
    this.subs.add(this.routerTrigger$.subscribe());
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  onActivate(e, outlet: RouterOutlet) {
    if (
      outlet.activatedRouteData &&
      outlet.activatedRouteData['animation'] &&
      outlet.activatedRouteData['index'] !== this.itemChanges$.getValue()
    ) {
      this.itemChanges$.next(outlet.activatedRouteData['index']);
    }
  }
}
