import { Component, OnInit } from '@angular/core';
import { BasePaymentContainer } from '@payments/containers/base-payment.container';
import { SocialPaymentFormModel } from '@payments/services/form-model';
import { PaymentsService } from '@payments/services/payments.service';
import {
  NgbDateParserFormatter,
  NgbModal,
  NgbDateStruct,
} from '@ng-bootstrap/ng-bootstrap';
import { MaskedService } from '@shared/masked/masked.service';
import { AccountsService, RegularAccount } from '@accounts/index';
import { tap, map } from 'rxjs/operators';
import { Observable, BehaviorSubject } from 'rxjs';
import { Employee } from '@shared/employee/employee.model';
import { SimpleStore } from '@utils/index';
import { PaymentResultComponent } from '@payments/components/payment-result/payment-result.component';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { CompanyService } from '@company/company.service';
import { PAYMENT_TYPE } from '@shared/models';
import { ActivatedRoute, Router } from '@angular/router';
import { PaymentApiService } from '@payments/services/payment-api.service';
import { PaymentsPipelineService } from '@payments/services/payments-pipeline.service';
import { PaymentsFormFactoryService } from '@payments/services/payments-form-factory.service';
import { TranslateService } from '@ngx-translate/core';
import { AuthorityPermissionsService } from '@authorities/services';
import { FeatureFlagsService } from '@feature-flags/index';

import {
  SOCIAL_EMPLOYEE_MAX_AMOUNT,
  SOCIAL_EMPLOYEE_MIN_AMOUNT,
} from '@payments/config/amounts';
import { BankFromAccountPipe } from '@shared/pipes';

@Component({
  selector: 'j-social-payment',
  templateUrl: './social-payment.component.html',
  styleUrls: ['./social-payment.component.scss'],
})
export class SocialPaymentComponent
  extends BasePaymentContainer<SocialPaymentFormModel>
  implements OnInit
{
  accountStore: SimpleStore<RegularAccount>;
  acceptableAccounts$: Observable<RegularAccount[]>;
  masks: {
    [k in keyof SocialPaymentFormModel]?: any;
  } = {
    number: 'paymentNum',
    amount: 'amount',
    knp: 'knp',
  };
  dateLimit: {
    minDate: NgbDateStruct;
    maxDate: NgbDateStruct;
  };

  employeeMinAmount = SOCIAL_EMPLOYEE_MIN_AMOUNT;
  employeeMaxAmount = SOCIAL_EMPLOYEE_MAX_AMOUNT;

  constructor(
    public paymentsService: PaymentsService,
    public companyService: CompanyService,
    public dateAdapter: NgbDateParserFormatter,
    protected route: ActivatedRoute,
    protected router: Router,

    protected maskService: MaskedService,
    protected accountsService: AccountsService,
    protected modalService: NgbModal,
    protected api: PaymentApiService,
    protected dictionaries: DictionariesService,
    protected pipeline: PaymentsPipelineService,
    protected translateService: TranslateService,
    protected paymentsFormsFactory: PaymentsFormFactoryService,
    protected featureFlagsService: FeatureFlagsService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected bankFromAccount: BankFromAccountPipe
  ) {
    super(
      paymentsFormsFactory.createSocialForm(),
      api,
      route,
      router,
      companyService,
      paymentsService,
      maskService,
      modalService,
      dictionaries,
      pipeline,
      translateService,
      authorityPermissionsService,
      featureFlagsService,
      bankFromAccount
    );
    this.paymentType = PAYMENT_TYPE.SOCIAL;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.dateLimit = this.paymentsService.stdDateRangeNgb;
    this.accountStore = this.accountsService.store;
    this.acceptableAccounts$ = this.paymentsService
      .combineAcceptableAccounts(this.accountStore)
      .pipe(tap((next) => this.checkPersonalAccounts(next)));
    this.getMaskConfig();
    this.createControlsStream(this.form);
    this.listenWorkingDays();
    this.combineEmployeesAmount();
    this.listenCommission();
    this.listenTotal();
    this.provideAmountMessage();
    this.listenDateMessageStream();
    this.listenPurposeMessageStream();

    this.knp$ = this.combineKNP();
  }

  // onEmployeeUpdate(items: Employee[]) {}

  // onEmployeeAdded(items: Employee[]) {
  //   this.formDirective.resetForm(this.formDirective.value)
  // }
}
