import { SimpleStore } from '@utils/index';

import { BasePaymentContainer } from '@payments/containers/base-payment.container';
import {
  PensionPaymentFormModel,
  PaymentsService,
  PaymentsPipelineService,
  PaymentApiService,
  PaymentsFormFactoryService,
} from '@payments/services';
import { TranslateService } from '@ngx-translate/core';
import { AuthorityPermissionsService } from '@authorities/services';
import { FeatureFlagsService } from '@feature-flags/index';
import {
  PENSION_EMPLOYEE_MAX_AMOUNT,
  PENSION_EMPLOYEE_MIN_AMOUNT,
} from '@payments/config/amounts';
import { BankFromAccountPipe } from '@shared/pipes';

@Component({
  selector: 'j-pension-payment',
  templateUrl: './pension-payment.component.html',
  styleUrls: ['./pension-payment.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PensionPaymentComponent
  extends BasePaymentContainer<PensionPaymentFormModel>
  implements OnInit, OnDestroy
{
  acceptableAccounts$: Observable<RegularAccount[]>;
  accountStore: SimpleStore<RegularAccount>;

  rules: PaymentsService['rules'];
  masks: {
    [k in keyof PensionPaymentFormModel]?: any;
  } = {
    number: 'paymentNum',
    amount: 'amount',
    knp: 'knp',
  };

  employeeMinAmount = PENSION_EMPLOYEE_MIN_AMOUNT;
  employeeMaxAmount = PENSION_EMPLOYEE_MAX_AMOUNT;

  readonly employees$ = new BehaviorSubject<Employee[]>([]);
  readonly amount$ = new BehaviorSubject<number>(0);
  readonly subs = new Subscription();

  constructor(
    protected route: ActivatedRoute,
    protected router: Router,
    public paymentsService: PaymentsService,
    public companyService: CompanyService,
    public dateAdapter: NgbDateParserFormatter,
    public maskService: MaskedService,
    private accountsService: AccountsService,
    private modalService: NgbModal,
    protected dictionaries: DictionariesService,
    protected api: PaymentApiService,
    protected pipeline: PaymentsPipelineService,
    protected translateService: TranslateService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected paymentsFormsFactory: PaymentsFormFactoryService,
    protected featureFlagsService: FeatureFlagsService,
    protected bankFromAccount: BankFromAccountPipe,
  ) {
    super(
      paymentsFormsFactory.createPensionForm(),
      api,
      route,
      router,
      companyService,
      paymentsService,
      maskService,
      modalService,
      dictionaries,
      pipeline,
      translateService,
      authorityPermissionsService,
      featureFlagsService,
      bankFromAccount
    );
    this.paymentType = PAYMENT_TYPE.PENSION;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.accountStore = this.accountsService.store;
    this.acceptableAccounts$ = this.paymentsService
      .combineAcceptableAccounts(this.accountStore)
      .pipe(tap((next) => this.checkPersonalAccounts(next)));

    this.getMaskConfig();
    this.createControlsStream(this.form);

    this.knp$ = this.combineKNP();

    this.combineEmployeesAmount();
    this.listenCommission();
    this.listenTotal();
    this.provideAmountMessage();
    this.listenDateMessageStream();
    this.listenPurposeMessageStream();
  }

  // sendPayment() {
  //   this.pipeline.processPayment({
  //     rawBody: {
  //       ...this.form.value,
  //       purpose: [this.form.value.purpose, this.purposeMessage$.getValue()]
  //         .filter((v) => !!v)
  //         .join('; '),
  //     },
  //     total: this.total$.getValue(),
  //     commission: this.commission$.getValue(),
  //   });
  // }

  // onEmployeeUpdate(items: Employee[]) {
  //   this.form.controls.employees.patchValue(items);
  //   // this.employees$.next(items);
  // }

  // onEmployeeAdded(items: Employee[]) {
  //   this.formDirective.resetForm(this.formDirective.value)
  // }
}
