const CITY_MAX_LENGTH_DEFAULT = 32;

const RUB_IBAN_LENGTH = 20;

const RUSSIAN_BUDGET_START_INDEX = '0';

const KNP_WITHOUT_CONTRACT = ['342', '343', '314', '312'];
// const RUSSIAN_BUDGET_MIDDLE_INDEX = '643';
const RUSSIAN_NON_BUDGET_MIDDLE_INDEX = '810';

const CONTAINS_LATIN_WORDS_REGEX = new RegExp(
  /(([a-zA-Z]{1}[a-zA-Z0-9\/\-?:().,\'+\ ]+)|([a-zA-Z]{1}))+/g
);

const IS_RUSSIAN_NON_BUDGET_IBAN_MIDDLE_INDEX = (iban: string) => {
  if (!iban) {
    return false;
  }
  return iban.substring(5, 8) !== RUSSIAN_NON_BUDGET_MIDDLE_INDEX;
};

const IS_RUSSIAN_BUDGET_BIC_MIDDLE_INDEX = (bic: string) => {
  if (!bic) {
    return false;
  }

  return ['000', '001', '002'].includes(bic.substring(6, 9));
};

const IS_RUSSIAN_BUDGET_IBAN_START_INDEX = (iban: string) => {
  if (!iban) {
    return false;
  }
  return iban[0] === RUSSIAN_BUDGET_START_INDEX;
};

const RUB_KIO_VALIDATOR = (control) => {
  if (!control.value) {
    return null;
  }

  if (
    control.value?.length !== 5 &&
    control.value?.length !== 10 &&
    control.value?.length !== 12
  ) {
    return {
      lengthMismatch: true,
    };
  }
  return null;
};

const RUB_IBAN_VALIDATOR = (control: AbstractControl, bic: string) => {
  const numericRegex = new RegExp('^[0-9]+$');

  if (control.value && !control.value.match(numericRegex)) {
    return {
      rubInvalidNumbers: true,
    };
    // }
    // else if (
    //   control.value &&
    //   control.value.length > 8 &&
    //   control.value[0] === RUSSIAN_BUDGET_START_INDEX &&
    //   control.value.substring(5, 8) === RUSSIAN_BUDGET_MIDDLE_INDEX &&
    //   isRussianBudgetDisabled
    // ) {
    //   return {
    //     rubBudgetDisabled: true,
    //   };
  } else if (
    control.value &&
    bic &&
    !IS_RUSSIAN_BUDGET_IBAN_START_INDEX(control.value) &&
    IS_RUSSIAN_BUDGET_BIC_MIDDLE_INDEX(bic)
  ) {
    return {
      rubInvalidBudget: true,
    };
  } else if (
    control.value &&
    bic &&
    control.value.length > 8 &&
    !IS_RUSSIAN_BUDGET_IBAN_START_INDEX(control.value) &&
    IS_RUSSIAN_NON_BUDGET_IBAN_MIDDLE_INDEX(control.value)
  ) {
    return {
      rubInvalid: true,
    };
  } else if (control.value && control.value.length !== RUB_IBAN_LENGTH) {
    return {
      rubLength: true,
    };
  }
  return null;
};

const DEFAULT_IBAN_VALIDATOR = (control: AbstractControl, country: string) => {
  const isIban = control.value?.substring(0, 2).match(/[A-Za-z]{2}/);

  if (
    country &&
    isIban &&
    control.value?.substring(0, 2).toUpperCase() !==
      country?.substring(0, 2).toUpperCase()
  ) {
    return {
      currencyMismatch: true,
    };
  }
  return null;
};

const RUB_NON_RUSSIA_IBAN_VALIDATOR = (control: AbstractControl) => {
  const numericRegex = new RegExp('^[0-9]+$');

  if (control.value && !control.value.match(numericRegex)) {
    return {
      rubInvalidNumbers: true,
    };
    // }
    // else if (
    //   control.value &&
    //   control.value.length > 8 &&
    //   control.value[0] === RUSSIAN_BUDGET_START_INDEX &&
    //   control.value.substring(5, 8) === RUSSIAN_BUDGET_MIDDLE_INDEX &&
    //   isRussianBudgetDisabled
    // ) {
    //   return {
    //     rubBudgetDisabled: true,
    //   };
    // } else if (control.value && !IS_RUSSIAN_BUDGET_IBAN_START_INDEX(control.value)) {
    // return {
    // rubInvalidBudget: true,
    // };
  } else if (
    control.value &&
    control.value.length > 8 &&
    !IS_RUSSIAN_BUDGET_IBAN_START_INDEX(control.value) &&
    IS_RUSSIAN_NON_BUDGET_IBAN_MIDDLE_INDEX(control.value)
  ) {
    return {
      rubInvalid: true,
    };
  } else if (control.value && control.value.length !== RUB_IBAN_LENGTH) {
    return {
      rubLength: true,
    };
  }
  return null;
};

