  // HOW IT SHOULD BE
  applyKbeRules() {
    this.subs.add(
      this.controlsStream.withinContract.subscribe((withinContract) => {
        if (withinContract) {
          this.clearValidation([this.form.controls.kbeCode]);
        } else {
          this.setValidation(
            [this.form.controls.kbeCode],
            [Validators.required, MiscValidators.kbe]
          );
        }
      })
    );
  }

  // AML Check
  applyAmlRules() {
    if (localStorage.getItem('aml-modal-shown')) {
      return;
    }

    localStorage.setItem('aml-modal-shown', 'true');

    this.modalService.open(AmlWarningModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });
  }

  // Name Rules
  applyNameRules() {
    const { name } = this.form.controls;
    const { withinContract, senderAccount } = this.controlsStream;

    this.subs.add(
      combineLatest([withinContract, senderAccount])
        .pipe(distinctUntilChanged())
        .subscribe((values) => {
          const [withinContractValue, senderAccountValue] = values;

          const currency = senderAccountValue?.balance?.currency;
          const prevPristine = name.pristine;

          if (withinContractValue) {
            name.disable();
            this.clearValidation([name]);
            this.masks.name = {
              mask: null,
            };
          } else {
            name.enable();
            this.setValidation([name], [Validators.required]);

            if (currency === CURRENCY.RUB) {
              this.masks.name = Masks.foreignCyrillic;
            } else {
              this.masks.name = Masks.foreignLatin;
            }
          }

          setTimeout(() => {
            if (prevPristine) {
              name.markAsPristine();
            }
          }, 0);
        })
    );
  }

  ibanParams(control) {
    if (!control?.errors?.bankRestricted) {
      return null;
    }

    return {
      bankName: this.bankFromAccount.transform(control.value, 'name'),
    };
  }

  // Name Events
  onNamePaste(event: ClipboardEvent) {
    event.preventDefault();

    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const toPasteRaw = pastedText.substring(0, this.nameMaxLength);

    this.form.controls.name.setValue(toPasteRaw);
  }

  // Country Rules
  applyCountryRules() {
    const { countryCode } = this.form.controls;
    const { withinContract, senderAccount, contract } = this.controlsStream;

    this.subs.add(
      combineLatest([withinContract, senderAccount, contract])
        .pipe(distinctUntilChanged())
        .subscribe((values) => {
          const [withinContractValue, senderAccountValue] = values;
          const currency = senderAccountValue?.balance?.currency;
          const prevPristine = countryCode.pristine;

          if (withinContractValue) {
            countryCode.disable();
            this.clearValidation([countryCode]);
          } else {
            countryCode.enable();
            const validators = [Validators.required];

            if (currency === CURRENCY.CNY) {
              validators.push((control) => {
                if (control.value !== COUNTRY.CN) {
                  return { cnyOnlyToChina: true };
                }
                return null;
              });
            }

            this.setValidation([countryCode], validators);
          }

          setTimeout(() => {
            if (prevPristine) {
              countryCode.markAsPristine();
            }
          }, 0);
        })
    );
  }

  // Country Events
  onCountrySearch({ term, items }) {
    if (!term) {
      this.countries$.next(this.countries);
      return;
    }

    const filteredCountries = this.countries.filter((country) => {
      return (
        country.code.toLowerCase().includes(term.toLowerCase()) ||
        country.nameEn.toLowerCase().includes(term.toLowerCase()) ||
        country.nameKk.toLowerCase().includes(term.toLowerCase()) ||
        country.nameRu.toLowerCase().includes(term.toLowerCase())
      );
    });

    const sortedCountries = filteredCountries.sort((a, b) => {
      return this.countrySorter(a, b, term);
    });

    this.countries$.next(sortedCountries);
  }

  onCountryFocus() {
    if (!this.form.controls.senderAccount.value) {
      this.senderAccountDropdown.hasError = true;
    }
  }

  onCountrySelect(country: ICountry) {
    this.hasDefaultCity = !!country.defaultCityName;
    this.form.controls.country.setValue(country);
  }

  // Country Dropdown Helpers
  countrySorter(a: ICountry, b: ICountry, term: string) {
    if (a.code.toLowerCase() === term.toLowerCase()) {
      return -1;
    } else if (b.code.toLowerCase() === term.toLowerCase()) {
      return 1;
    } else if (a.code.toLowerCase().includes(term.toLowerCase())) {
      return -1;
    } else if (b.code.toLowerCase().includes(term.toLowerCase())) {
      return 1;
    } else {
      switch (this.translateService.currentLang) {
        case 'ru':
          return a.nameRu < b.nameRu ? -1 : 1;
        case 'en':
          return a.nameEn < b.nameEn ? -1 : 1;
        case 'kk':
          return a.nameKk < b.nameKk ? -1 : 1;
        default:
          return 0;
      }
    }
  }

  countryComparator(query: string, country: ICountry) {
    return true;
  }

  mapCountryLabel(country: ICountry) {
    let countryName;

    switch (this.translateService.currentLang) {
      case 'ru':
      default:
        countryName = country.nameRu;
        break;
      case 'kk':
        countryName = country.nameKk;
        break;
      case 'en':
        countryName = country.nameEn;
        break;
    }

    return `${country.code} - ${countryName || country.name}`;
  }

  // Country Helpers
  private get currentCountryCode(): COUNTRY {
    const contractCountryCode =
      this.formValue.contract?.contractor?.country?.code || null;

    if (this.formValue.withinContract && contractCountryCode) {
      return contractCountryCode.substring(0, 2) as COUNTRY;
    }

    if (!this.formValue.withinContract && this.formValue.countryCode) {
      return this.formValue.countryCode.substring(0, 2) as COUNTRY;
    }

    return null;
  }

  private async retrieveCountries() {
    this.countries = await this.currencyPaymentService.getCountries();
    this.countries$.next(this.countries);
  }

  // BankAccount (IBAN)
  applyIbanRules() {
    const {
      senderAccount,
      country,
      countryCode,
      contract,
      contractId,
      bank,
      rubNonRussiaBank,
      withinContract,
    } = this.controlsStream;
    this.subs.add(
      combineLatest([
        senderAccount,
        bank,
        rubNonRussiaBank,
        country,
        countryCode,
        contract,
        contractId,
        withinContract,
        this.countries$,
      ]).subscribe((values) => {
        const [senderAccount, bank, rubNonRussiaBank] = values;

        const validators = [Validators.required];

        const asyncValidators = [
          PaymentAsyncValidators.externalIbanRestricted(this.dictionaries),
        ];

        const senderCurrency = (senderAccount as RegularAccount)?.balance
          ?.currency;
        const currentCountryCode = this.currentCountryCode;
        const prevPristine = this.form.controls.bankAccount.pristine;

        if (
          senderCurrency === CURRENCY.RUB &&
          currentCountryCode === COUNTRY.RU
        ) {
          this.masks.bankAccount = Masks.russianAccount;

          validators.push((control: AbstractControl) => {
            return RUB_IBAN_VALIDATOR(control, (bank as IBank)?.bic);
          });
        } else {
          const bankToCompare =
            senderCurrency === CURRENCY.RUB ? rubNonRussiaBank : bank;

          const bankCountryCode = this.countries.find(
            (country) => country.id === (bankToCompare as IBank)?.countryId
          )?.code;

          this.masks.bankAccount = Masks.foreignAccount;

          validators.push((control: AbstractControl) => {
            return DEFAULT_IBAN_VALIDATOR(control, bankCountryCode);
          });

          validators.push(Validators.maxLength(this.ibanMaxLength));
        }

        this.setValidation(
          [this.form.controls.bankAccount],
          validators,
          asyncValidators
        );

        setTimeout(() => {
          if (prevPristine) {
            this.form.controls.bankAccount.markAsPristine();
          }
        }, 0);
      })
    );
  }

