  public codeWordComparator(query: string, codeWord: IDictionaryValue) {
    if (!query) {
      return true;
    }

    return (
      codeWord.name.toLowerCase().includes(query.toLowerCase()) ||
      codeWord.code.toLowerCase().includes(query.toLowerCase())
    );
  }

  public kbkComparator(query: string, kbk: IRusDictionary) {
    if (!query) {
      return true;
    }

    return (
      kbk.code.toLowerCase().includes(query.toLowerCase()) ||
      kbk.description.toLowerCase().includes(query.toLowerCase())
    );
  }

  public statusComparator(query: string, status: IRusDictionary) {
    if (!query) {
      return true;
    }

    return (
      status.code.toLowerCase().includes(query.toLowerCase()) ||
      status.description.toLowerCase().includes(query.toLowerCase())
    );
  }

