  private async updateContractsSwitchDescriptionParams(
    senderCurrency: CURRENCY = CURRENCY.USD
  ) {
    if (!senderCurrency || senderCurrency === CURRENCY.USD) {
      this.contractMaxAmount = {
        amount: 10000,
        currency: CURRENCY.USD,
      };
    } else {
      this.isContractsDescriptionLoading = true;

      const exchangeRate: IExchangeRate = {
        currencyFrom: senderCurrency,
        currencyTo: CURRENCY.USD,
        sum: {
          amount: 10000,
          currency: CURRENCY.USD,
        },
      };
      try {
        const calculatedRate = await this.conversionService.getRate(
          exchangeRate
        );

        this.contractMaxAmount = {
          amount: calculatedRate.calculatedSum,
          currency: senderCurrency,
        };
      } catch (error) {
        this.contractMaxAmount = {
          amount: 10000,
          currency: CURRENCY.USD,
        };
      } finally {
        this.isContractsDescriptionLoading = false;
      }
    }

    let amount = this.maskService.getMaskedAmount(
      this.contractMaxAmount.amount
    );

    amount = amount
      .split(',')
      .filter((number) => parseInt(number, 10) > 0)
      .map((number, index) => {
        if (index === 1 && number.length < 2) {
          return `${number}0`;
        }
        return number;
      })
      .join(',');

    const currency = this.curSymbolPipe.transform(
      this.contractMaxAmount.currency
    );

    this.contractsSwitchDescriptionParams = {
      amount: `${amount} ${currency}`,
    };
  }

  async openForeignRecipientsDictionary() {
    const modalRef = this.modals.open(ForeignRecipientsDictionaryComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_dictionary',
      centered: true,
    });

    try {
      const selectedRecipient = (await modalRef.result) as ISelectedRecipient;

      const { countryCode, cityName, name, bankAccount, kbeCode, bank, kio } =
        this.form.controls;

      const { recipient, account } = selectedRecipient;

      countryCode.setValue(recipient.countryCode);
      cityName.setValue(recipient.cityName || '');
      name.setValue(recipient.name || '');
      bankAccount.setValue(account.iban);
      kbeCode.setValue(recipient.kbe.code);

      bank.setValue({
        bic: account.bic,
        name: account.bankName,
      });

      this.bankDropdown.selectByBic(account.bic);
      this.rubNonRussiaBankDropdown?.selectByBic(account.bic);

      kio?.setValue(recipient.iinOrBin);
    } catch (error) {}
  }

  get formValue(): CurrencyPaymentFormModel {
    return this.form.getRawValue();
  }

  get shouldBindContract(): boolean {
    if (
      KNP_WITHOUT_CONTRACT.indexOf(this.formValue.knp) !== -1 ||
      this.isForeignContractDisabled
    ) {
      return false;
    }
    return (
      this.formValue.senderAccount?.balance?.currency ===
        this.contractMaxAmount.currency &&
      !this.formValue.contractId &&
      this.maskService.getNumberedAmount(this.formValue.amount) >
        this.contractMaxAmount.amount
    );
  }

  private applyAmountValidation() {
    this.subs.add(
      combineLatest([
        this.controlsStream.senderAccount,
        this.controlsStream.contractId,
      ]).subscribe((values) => {
        const [senderAccount, contractId] = values;

        let validators = [
          Validators.required,
          Validators.min(this.paymentsService.rules.minAmount),
          Validators.max(this.paymentsService.rules.maxAmount),
          (control: AbstractControl) => {
            if (control?.value === 0) {
              return { cantBeZero: true };
            }
            return null;
          },
        ];

        if (
          senderAccount?.balance?.currency ===
            this.contractMaxAmount.currency &&
          !contractId
        ) {
          validators.push((control) => {
            const numberedAmount = this.maskService.getNumberedAmount(
              control.value
            );
            if (this.isForeignContractDisabled || this.withinContract) {
              return;
            } else {
              if (numberedAmount > this.contractMaxAmount.amount) {
                if (KNP_WITHOUT_CONTRACT.indexOf(this.formValue.knp) !== -1) {
                  return null;
                } else {
                  return {
                    mustIncludeContract: true,
                  };
                }
              }
            }
            //          , .
            // else if (
            //   numberedAmount > formValue.senderAccount?.balance?.amount
            // ) {
            //   return {
            //     balanceNotEnough: true,
            //   };
            // }
            return null;
          });
        }

        this.setValidation(
          [this.form.controls.amount],
          validators,
          null,
          false
        );
      })
    );
  }

  private setValidation(
    controls: AbstractControl[],
    validators: ValidatorFn[] | ValidatorFn,
    asyncValidators: AsyncValidatorFn[] | AsyncValidatorFn = [],
    emitEvent = true
  ) {
    controls.forEach((control) => {
      const prevPristine = control.pristine;

      control.setValidators(validators);
      control.setAsyncValidators(asyncValidators);
      control.updateValueAndValidity({ emitEvent });

      setTimeout(() => {
        if (prevPristine) {
          control.markAsPristine();
        }
      }, 0);
    });
  }

  private clearValidation(controls: AbstractControl[], emitEvent = true) {
    controls.forEach((control) => {
      const prevPristine = control.pristine;

      control.clearValidators();
      control.clearAsyncValidators();
      control.updateValueAndValidity({ emitEvent });

      setTimeout(() => {
        if (prevPristine) {
          control.markAsPristine();
        }
      }, 0);
    });
  }

