import { Location } from '@angular/common';
import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { PaymentsService } from '@payments/services/payments.service';
import {
  ErrorStateMatcher,
  ShowOnDirtyErrorStateMatcher,
} from '@shared/form-field/utils';
import { NavService } from '@shared/nav/nav.service';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { CurrencyPaymentComponent } from '../currency-payment/currency-payment.component';

@Component({
  selector: 'j-currency-payments',
  templateUrl: './currency-payments.component.html',
  styleUrls: ['./currency-payments.component.scss'],
  providers: [
    {
      provide: ErrorStateMatcher,
      useClass: ShowOnDirtyErrorStateMatcher,
    },
  ],
})
export class CurrencyPaymentsComponent implements OnInit, AfterViewInit {
  title$: Observable<string>;
  @ViewChild('paymentComponent') paymentComponent: CurrencyPaymentComponent;
  // get child form controls to activate unsaved changes guard
  formDirective: NgForm;
  form: FormGroup;

  constructor(
    private location: Location,
    private navService: NavService,
    private paymentsService: PaymentsService
  ) {}

  ngOnInit() {
    this.title$ = this.paymentsService.rootTitle$.pipe(
      map((v) => `CURRENCY_PAYMENT.${v}`)
    );
  }

  onClose() {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();
    
    this.location.back();
  }

  ngAfterViewInit() {
    this.formDirective = this.paymentComponent.formDirective;
    this.form = this.paymentComponent.form;
  }

}
