import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { HomeRoutes, homeRoutes } from '@home/home-routes';

import { routeTransitionAnimations } from '@shared/animations';
import { RouterSlideAnimation } from '@shared/animations/helpers';
import {
  ErrorStateMatcher,
  ShowOnDirtyErrorStateMatcher,
} from '@shared/form-field/utils';
import { map, debounceTime } from 'rxjs/operators';
import { PaymentApiService } from '@payments/services/payment-api.service';
import { PaymentsRoutes, paymentsRoutes } from '@payments/payments-routes';
import { PaymentsService } from '@payments/services/payments.service';
import { Observable } from 'rxjs';
import { NavService } from '@shared/nav/nav.service';
import { Location } from '@angular/common';

@Component({
  selector: 'j-tenge-payments',
  templateUrl: './tenge-payments.component.html',
  styleUrls: ['./tenge-payments.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [routeTransitionAnimations],
  providers: [
    {
      provide: ErrorStateMatcher,
      useClass: ShowOnDirtyErrorStateMatcher,
    },
  ],
})
export class TengePaymentsComponent
  extends RouterSlideAnimation
  implements OnInit
{
  routes: PaymentsRoutes = paymentsRoutes;
  title$: Observable<string>;
  constructor(
    private navService: NavService,
    private router: Router,
    private paymentsService: PaymentsService,
    private location: Location
  ) {
    super();
  }

  ngOnInit() {
    super.ngOnInit();

    this.title$ = this.paymentsService.rootTitle$.pipe(
      map((v) => `PAYMENTS.${v}`)
    );
  }

  onClose() {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();

    this.location.back();
  }
}
