import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {
  tengePaymentsRoutes,
  statePaymentsRoutes,
  paymentsRoutes,
} from './payments-routes';

import { factoryUserPageMatcher } from '@utils/index';

import { CanDeactivatePaymentGuard } from './services/can-deactivate-payment.guard';
import { PaymentTypeSwitchGuard } from './services/payment-type-switch.guard';

import {
  TengePaymentsComponent,
  TaxPaymentComponent,
  BudgetPaymentsComponent,
  SalaryPaymentComponent,
  PaymentOrderComponent,
  CurrencyPaymentsComponent,
  SocialPaymentComponent,
  PensionPaymentComponent,
  MedicalPaymentComponent,
} from './containers';

const paymentId = '/:paymentId';

const routes: Routes = [
  {
    path: tengePaymentsRoutes.root,
    component: TengePaymentsComponent,
    children: [
      {
        path: '',
        canActivate: [PaymentTypeSwitchGuard],
      },
      {
        path: statePaymentsRoutes.root,
        component: BudgetPaymentsComponent,
        data: { animation: 'slideOrFade', index: 2 },
        children: [
          {
            path: statePaymentsRoutes.tax,
            matcher: factoryUserPageMatcher(
              statePaymentsRoutes.tax,
              'paymentId'
            ),
            component: TaxPaymentComponent,
            canDeactivate: [CanDeactivatePaymentGuard],
            data: { animation: 'slideOrFade', index: 21 },
          },
          {
            path: statePaymentsRoutes.pension,
            matcher: factoryUserPageMatcher(
              statePaymentsRoutes.pension,
              'paymentId'
            ),
            component: PensionPaymentComponent,
            canDeactivate: [CanDeactivatePaymentGuard],
            data: { animation: 'slideOrFade', index: 22 },
          },

          {
            path: statePaymentsRoutes.social,
            matcher: factoryUserPageMatcher(
              statePaymentsRoutes.social,
              'paymentId'
            ),
            component: SocialPaymentComponent,
            canDeactivate: [CanDeactivatePaymentGuard],
            data: { animation: 'slideOrFade', index: 23 },
          },

          {
            path: statePaymentsRoutes.osms,
            matcher: factoryUserPageMatcher(
              statePaymentsRoutes.osms,
              'paymentId'
            ),
            component: MedicalPaymentComponent,
            canDeactivate: [CanDeactivatePaymentGuard],
            data: { animation: 'slideOrFade', index: 24 },
          },
          {
            path: '**',
            redirectTo: statePaymentsRoutes.tax,
          },
        ],
      },
      {
        path: tengePaymentsRoutes.order,
        matcher: factoryUserPageMatcher(tengePaymentsRoutes.order, 'paymentId'),
        component: PaymentOrderComponent,
        canDeactivate: [CanDeactivatePaymentGuard],
        data: { animation: 'slideOrFade', index: 1 },
      },
      {
        path: tengePaymentsRoutes.salary,
        matcher: factoryUserPageMatcher(
          tengePaymentsRoutes.salary,
          'paymentId'
        ),
        component: SalaryPaymentComponent,
        canDeactivate: [CanDeactivatePaymentGuard],
        data: { animation: 'slideOrFade', index: 3 },
      },
      // catch case when we have only id without type or provide easy navigation by id
      // and restore state without explicit direct payment type route
      {
        path: ':paymentId',
        canActivate: [PaymentTypeSwitchGuard],
      },
      {
        path: '**',
        redirectTo: tengePaymentsRoutes.order,
      },
    ],
  },
  {
    path: paymentsRoutes.foreignCurrency,
    canDeactivate: [CanDeactivatePaymentGuard],
    matcher: factoryUserPageMatcher(
      paymentsRoutes.foreignCurrency,
      'paymentId'
    ),
    component: CurrencyPaymentsComponent,
  },
  {
    path: '**',
    redirectTo: tengePaymentsRoutes.root,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PaymentsRoutingModule {}
