import { RegularAccount, AccountsService } from '@accounts/index';
import {
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  TemplateRef,
} from '@angular/core';
import { NgForm } from '@angular/forms';
import {
  NgbDateParserFormatter,
  NgbDateStruct,
  NgbModal,
} from '@ng-bootstrap/ng-bootstrap';
import { BasePaymentContainer } from '@payments/containers/base-payment.container';
import { GenericDictionaryComponent } from '@payments/components/generic-dictionary/generic-dictionary.component';
import { UgdDictionaryComponent } from '@payments/components/ugd-dictionary/ugd-dictionary.component';
import { TaxPaymentFormModel } from '@payments/services/form-model';
import { PaymentsService } from '@payments/services/payments.service';
import { MaskedService } from '@shared/masked/masked.service';
import { KBK, KNP, UGD } from '@shared/models/dictionary';
import { SimpleStore } from '@utils/index';
import {
  BehaviorSubject,
  Observable,
  Subscription,
  combineLatest,
} from 'rxjs';
import {
  tap,
  debounceTime,
  distinctUntilChanged,
  throttleTime,
  map,
} from 'rxjs/operators';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { CompanyService } from '@company/company.service';
import { PAYMENT_TYPE, SPECIAL_ACCOUNTS } from '@shared/models';
import { FocusableContainerDirective } from '@shared/directives';
import { TranslateService } from '@ngx-translate/core';
import { DatePipe } from '@angular/common';
import { PaymentsPipelineService } from '@payments/services/payments-pipeline.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PaymentApiService } from '@payments/services/payment-api.service';
import { PaymentsFormFactoryService } from '@payments/services/payments-form-factory.service';
import { JSelectComponent } from '@shared/select-module/select.component';
import { KnpDictionaryComponent } from '@payments/components';
import { AuthorityPermissionsService } from '@authorities/services';
import { FeatureFlagsService } from '@feature-flags/index';
import { BankFromAccountPipe } from '@shared/pipes';

@Component({
  selector: 'j-tax-payment',
  templateUrl: './tax-payment.component.html',
  styleUrls: ['./tax-payment.component.scss'],
})
export class TaxPaymentComponent
  extends BasePaymentContainer<TaxPaymentFormModel>
  implements OnInit, OnDestroy
{
  @ViewChild(NgForm)
  ngForm: NgForm;
  @ViewChild(FocusableContainerDirective)
  focusableContainer: FocusableContainerDirective;

  @ViewChild('vinTpl', { read: TemplateRef })
  vinTpl: TemplateRef<any>;
  @ViewChild('protocolNumberTpl', { read: TemplateRef })
  protocolNumberTpl: TemplateRef<any>;

  associatedFieldTpl: TemplateRef<any> = undefined;

  accountStore: SimpleStore<RegularAccount>;
  acceptableAccounts$: Observable<RegularAccount[]>;
  masks: {
    [k in keyof TaxPaymentFormModel]?: any;
  } = {
    number: 'paymentNum',
    amount: 'amount',
    vin: 'vin',
    knp: 'knp',
    kbk: 'kbk',
    protocolNumber: 'protocol',
    bankAccount: 'tengeAccount',
  };
  dateLimit: {
    minDate: NgbDateStruct;
    maxDate: NgbDateStruct;
  };

  // selectedAccount$: Observable<Account>;
  commission$ = new BehaviorSubject(0);

  controlsStream: {
    [k in keyof TaxPaymentFormModel]: Observable<TaxPaymentFormModel[k]>;
  };

  protocolMessage$ = new BehaviorSubject('');
  filteredKnpList: KNP[];
  vinLength = 17;

  readonly subs = new Subscription();
  constructor(
    protected route: ActivatedRoute,
    protected router: Router,
    public paymentsService: PaymentsService,
    public companyService: CompanyService,
    public dateAdapter: NgbDateParserFormatter,
    public maskService: MaskedService,
    public translate: TranslateService,
    public datePipe: DatePipe,
    private accountsService: AccountsService,
    private modalService: NgbModal,
    private pipelines: PaymentsPipelineService,
    protected api: PaymentApiService,
    protected dictionaries: DictionariesService,
    protected featureFlagsService: FeatureFlagsService,
    protected paymentsFormsFactory: PaymentsFormFactoryService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected bankFromAccount: BankFromAccountPipe
  ) {
    super(
      paymentsFormsFactory.createTaxForm(),
      api,
      route,
      router,
      companyService,
      paymentsService,
      maskService,
      modalService,
      dictionaries,
      pipelines,
      translate,
      authorityPermissionsService,
      featureFlagsService,
      bankFromAccount
    );
    this.paymentType = PAYMENT_TYPE.TAX;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.dateLimit = this.paymentsService.stdDateRangeNgb;

    this.accountStore = this.accountsService.store;
    this.acceptableAccounts$ = this.paymentsService
      .combineAcceptableAccounts(this.accountStore)
      .pipe(tap((next) => this.checkPersonalAccounts(next)));

    this.getMaskConfig();
    this.createControlsStream(this.form);

    this.combineKBK(this.paymentType);
    this.knp$ = this.combineKNP([
      (knps) =>
        this.dictionaries.selectByPaymentType(knps, {
          type: PAYMENT_TYPE.TAX,
          sortFavourite: false,
        }),
    ]);

    this.listenWorkingDays();
    this.listenCommission();
    this.listenTotal();
    this.provideAmountMessage();
    this.listenDateMessageStream();
    this.listenKnpUpdates();

    this.subs.add(this.createPeriodMessage$().subscribe(this.periodMessage$));
    this.subs.add(
      combineLatest([
        this.controlsStream.vin.pipe(map((v) => (v ? `VIN${v}/V` : ''))),
        this.controlsStream.protocolNumber.pipe(
          map((v) => (v ? `ADM${v}/A` : ''))
        ),
      ]).subscribe((values) => {
        this.protocolMessage$.next(values.join(' '));
      })
    );

    const purposeParts = [
      this.protocolMessage$.pipe(map((message) => ['protocol', message])),
      combineLatest([
        this.translate.get('SHARED.NOT_SELECTED'),
        this.translate.get('SHARED.TIME.PERIOD'),
        this.periodMessage$,
      ]).pipe(
        map(([NOT_SELECTED, PERIOD, message]) => {
          if (message && message !== NOT_SELECTED) {
            return `${PERIOD}: ${message}.`;
          }
          return '';
        }),
        map((message) => ['period', message])
      ),
    ];
    this.listenPurposeMessageStream(purposeParts, [
      'protocol',
      'kbk',
      'knp',
      'period',
    ]);

    this.subs.add(
      this.controlsStream.kbk.pipe().subscribe((code) => {
        const { protocolNumber, vin } = this.form.controls;
        if (this.rules.kbkCodeVinReq.some((item) => item === code)) {
          vin.enable();
          protocolNumber.disable();
          protocolNumber.value && protocolNumber.patchValue('');
          this.associatedFieldTpl = this.vinTpl;
        } else if (/^204\d+$/.test(code)) {
          vin.disable();
          vin.value && vin.patchValue('');
          protocolNumber.enable();
          this.associatedFieldTpl = this.protocolNumberTpl;
        } else {
          this.associatedFieldTpl = null;
          vin.disable();
          protocolNumber.disable();
          protocolNumber.value && protocolNumber.patchValue('');
          vin.value && vin.patchValue('');
        }
      })
    );

    this.subs.add(
      this.controlsStream.intervalEnabled
        .pipe(
          throttleTime(300, undefined, { leading: true, trailing: true }),
          distinctUntilChanged()
        )
        .subscribe((v) => {
          const { period, periodEnd, periodStart } = this.form.controls;
          if (v) {
            [periodEnd, periodStart].forEach((c) => c.enable());
            period.disable();
          } else {
            [periodEnd, periodStart].forEach((c) => c.disable());
            period.enable();
          }
        })
    );
    if (!this.form.get('bankAccount').value) {
      this.form.get('bankAccount').setValue(SPECIAL_ACCOUNTS.TAX_ACCOUNT);
    }
  }

  get intervalSwitchLabel() {
    return this.form.controls.intervalEnabled.value
      ? 'PAYMENTS.INTERVAL.ENABLED'
      : 'PAYMENTS.INTERVAL.DISABLED';
  }

  onKbkSelect(kbk: KBK) {
    if (!this.form.controls.knp.value && kbk.associatedKnpList) {
      this.form.controls.knp.setValue(kbk.associatedKnpList[0]);
    }
  }

  openDepartmentDictionary(select, event?: Event) {
    event && event.preventDefault();
    if (select) {
      select.blur();
      select.close();
    }
    this.modalService
      .open(UgdDictionaryComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_max-height',
        centered: true,
      })
      .result.then((item: UGD) => {
        this.form.controls.ugd.setValue(item.code);
      })
      .catch(() => {
        /*  no selection or dismiss */
      });
  }

  openKBKDictionary(select, event?: Event) {
    event && event.preventDefault();
    if (select) {
      select.blur();
      select.close();
    }
    const modal = this.modalService.open(GenericDictionaryComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_max-height',
      centered: true,
    });
    Object.assign(modal.componentInstance, {
      store: this.kbkStore,
    });
    modal.result
      .then((item: KBK) => {
        this.form.controls.kbk.setValue(item.code);
      })
      .catch(() => {
        /*  no selection or dismiss */
      });
  }

  onUgdSelect(ugd: UGD) {
    // this.form.controls.knp.patchValue(ugd.knpCode);
  }

  // showResult() {
  //   const modal = this.modalService.open(PaymentResultComponent, {
  //     backdropClass: 'backdrop_light',
  //     windowClass: 'modal_light modal_sm',
  //     centered: true,
  //     backdrop: 'static',
  //   });
  //   Object.assign(modal.componentInstance, {
  //     result: true,
  //     currency: 'KZT',
  //     amount: +this.form.controls.amount.value + this.commission$.getValue(),
  //     multySubscribers: false,
  //     hasRights: true,
  //   });
  // }

  createPeriodMessage$() {
    const { intervalEnabled, periodEnd, periodStart, period } =
      this.controlsStream;
    return combineLatest([
      intervalEnabled,
      this.translate.get('SHARED.NOT_SELECTED'),
      period,
      periodStart,
      periodEnd,
    ]).pipe(
      debounceTime(0),
      map(([intervalEnabledV, notSelected, ...rest]) => {
        const [periodV, periodStartV, periodEndV] = rest.map((v) =>
          v ? this.datePipe.transform(v, 'MM.yyyy') : ''
        );
        if (
          (!intervalEnabledV && !periodV) ||
          (intervalEnabledV && !periodStartV && !periodEndV)
        ) {
          return notSelected;
        }
        if (intervalEnabledV) {
          return `${periodStartV || notSelected} - ${
            periodEndV || notSelected
          }`;
        }
        return `${periodV || notSelected}`;
      })
    );
  }

  sendPayment() {
    return super.sendPayment(this.constructTaxPaymentBody());
  }

  saveToDraft() {
    return super.saveToDraft(this.constructTaxPaymentBody());
  }

  public openKnpDictionary(select: JSelectComponent, event: Event) {
    select.close();
    select.blur();
    setTimeout(() => {
      const modalRef = this.modals.open(KnpDictionaryComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_max-height',
        centered: true,
      });

      modalRef.componentInstance.paymentType = this.paymentType;
      modalRef.result
        .then((item: KNP) => {
          this.form.controls.knp.patchValue(item.code);
        })
        .catch(() => {
          /*  no selection or dismiss */
        });
    }, 0);
  }

  private constructTaxPaymentBody() {
    const formValue = this.form.value;
    const protocol = this.protocolMessage$.getValue();

    let purpose = '';
    if (protocol) {
      purpose = this.purposeMessage$
        .getValue()
        .replace(
          protocol,
          [protocol, formValue.purpose].filter((v) => !!v).join(' ')
        );
    } else {
      purpose = [formValue.purpose, this.purposeMessage$.getValue()]
        .filter((v) => !!v)
        .join('. ');
    }
    return {
      ...formValue,
      purpose,
    };
  }

  private listenKnpUpdates() {
    this.knp$.subscribe((list) => {
      this.filteredKnpList = list;
    });
  }

  get vinMessage() {
    if (
      !this.form.controls.vin.value ||
      this.form.controls.vin.value.length === 0
    ) {
      return this.translate.instant('PAYMENTS.VIN_MESSAGE.OPTIONAL');
    }
    if (this.form.controls.vin.value?.length < this.vinLength) {
      return this.translate.instant('PAYMENTS.VIN_MESSAGE.LESS', {
        length: this.vinLength,
      });
    }
    if (this.form.controls.vin.value?.length > this.vinLength) {
      return this.translate.instant('PAYMENTS.VIN_MESSAGE.MORE', {
        length: this.vinLength,
      });
    }
    return '';
  }
}

