import { AccountsService, RegularAccount } from '@accounts/index';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CompanyService } from '@company/company.service';
import {
  Recipient,
  RecipientAccount,
} from '@company/recipient/recipient.model';
import {
  NgbDateParserFormatter,
  NgbModal,
  NgbTypeahead,
} from '@ng-bootstrap/ng-bootstrap';
import { RecipientsDictionaryComponent } from '@payments/components/recipients-dictionary/recipients-dictionary.component';
import { BasePaymentContainer } from '@payments/containers/base-payment.container';
import { OrderPaymentFormModel } from '@payments/services/form-model';
import { PaymentsPipelineService } from '@payments/services/payments-pipeline.service';
import { PaymentsService } from '@payments/services/payments.service';
import { MaskedService } from '@shared/masked/masked.service';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { SimpleStore } from '@utils/index';
import {
  BehaviorSubject,
  combineLatest,
  Observable,
  EMPTY,
  of,
  Subject,
  merge,
} from 'rxjs';
import {
  debounceTime,
  filter,
  map,
  take,
  tap,
  startWith,
  switchMap,
  distinctUntilChanged,
  catchError,
  withLatestFrom,
  debounce,
  finalize,
} from 'rxjs/operators';
import {
  IFxContract,
  IFxContractRecipient,
  KNP,
  PAYMENT_TYPE,
  User,
} from '@shared/models';
import { PaymentApiService } from '@payments/services/payment-api.service';
import { RecipientsService, ICounterParty } from '@company/recipients.service';
import { PaymentsFormFactoryService } from '@payments/services/payments-form-factory.service';
import { JSelectComponent } from '@shared/select-module/select.component';
import {
  FxContractDetailsComponent,
  IinCheckErrorComponent,
  KnpDictionaryComponent,
} from '@payments/components';
import { UserService } from '@shared/services';
import { TranslateService } from '@ngx-translate/core';
import { Account } from '@home/models/account';
import { AuthorityPermissionsService } from '@authorities/services';
import { FeatureFlagsService } from '@feature-flags/index';
import { ContractsModalsService } from '@contracts/services';
import { CurrencyPaymentService } from '@payments/services';
import { Validators } from '@angular/forms';
import { CURRENCY } from '@core/models';
import { BankFromAccountPipe } from '@shared/pipes';

enum COUNTER_PARTY_STATUS {
  SUCCESS = 'SUCCESS',
  ERROR = 'ERROR',
}

@Component({
  selector: 'j-payment-order',
  templateUrl: './payment-order.component.html',
  styleUrls: ['./payment-order.component.scss'],
})
export class PaymentOrderComponent
  extends BasePaymentContainer<OrderPaymentFormModel>
  implements OnInit, OnDestroy
{
  acceptableAccounts$: Observable<RegularAccount[]>;
  accountStore: SimpleStore<RegularAccount>;

  rules: PaymentsService['rules'];

  loadingByiin$: Observable<boolean>;
  selectedAccount$: Observable<Account>;

  isCounterPartyLoading$ = new BehaviorSubject<string | null>(null);
  isContractApplyable$: Observable<boolean>;

  masks: {
    [k in keyof OrderPaymentFormModel]?: any;
  } = {
    iinOrBin: 'iin',
    bankAccount: 'tengeAccount',
    kbeCode: 'kbe',
    knp: 'knp',
    number: 'paymentNum',
    amount: 'amount',
  };

  riskGroups: { id: string; name: string }[] = [];
  user: User;
  counterPartyStatus = COUNTER_PARTY_STATUS;
  counterParty$ = new BehaviorSubject<COUNTER_PARTY_STATUS>(null);

  focusEmail$ = new Subject<string>();
  prevEmails$ = new BehaviorSubject([]);
  fetchingEmailsFor$ = new BehaviorSubject(null);

  search: (text$: Observable<string>) => Observable<any>;

  @ViewChild('emailOnSuccess', { static: true }) emailOnSuccess: NgbTypeahead;
  iinIsNotOwn;
  iinIsSubmitted$ = new BehaviorSubject<boolean>(null);
  iinIsFound$ = new BehaviorSubject<boolean>(null);

  isFxContractsLoading = true;
  fxContracts: IFxContract[] = [];

  @ViewChild('contractDetails')
  contractDetails: FxContractDetailsComponent;

  constructor(
    public companyService: CompanyService,
    public paymentsService: PaymentsService,
    public maskService: MaskedService,

    protected route: ActivatedRoute,
    protected router: Router,
    protected api: PaymentApiService,
    protected dictionaries: DictionariesService,
    protected translateService: TranslateService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected featureFlagsService: FeatureFlagsService,
    protected bankFromAccount: BankFromAccountPipe,

    private accountsService: AccountsService,
    private modalService: NgbModal,
    private pipelines: PaymentsPipelineService,
    private paymentsFormsFactory: PaymentsFormFactoryService,

    private recipientsService: RecipientsService,
    private userService: UserService,
    private contractsModalsService: ContractsModalsService,
    private currencyPaymentService: CurrencyPaymentService
  ) {
    super(
      paymentsFormsFactory.createPaymentInTengeForm(),
      api,
      route,
      router,
      companyService,
      paymentsService,
      maskService,
      modalService,
      dictionaries,
      pipelines,
      translateService,
      authorityPermissionsService,
      featureFlagsService,
      bankFromAccount
    );
    this.paymentType = PAYMENT_TYPE.REGULAR_PAYMENT_ORDER;
    this.user = this.userService.user;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.dateLimit = this.paymentsService.stdDateRangeNgb;
    this.accountStore = this.accountsService.store;
    this.acceptableAccounts$ = this.paymentsService
      .combineAcceptableAccounts(this.accountStore)
      .pipe(tap((next) => this.checkPersonalAccounts(next)));

    this.knp$ = this.combineKNP([
      (knps) =>
        this.dictionaries.selectByPaymentType(knps, {
          type: PAYMENT_TYPE.REGULAR_PAYMENT_ORDER,
          sortFavourite: true,
        }),
    ]);
    this.listenWorkingDays();
    this.getMaskConfig();
    this.createControlsStream(this.form);
    this.listenVAT();
    this.listenCommission();
    this.listenTotal();
    this.provideAmountMessage();
    this.listenDateMessageStream();
    this.listenPurposeMessageStream();
    this.listenEmailSearch();
    this.retrieveContracts();

    const { bankAccount, senderAccount } = this.controlsStream;
    this.subs.add(
      combineLatest([
        bankAccount.pipe(
          filter((v) => this.form.controls.bankAccount.valid),
          startWith('')
        ),
        senderAccount.pipe(
          filter((v) => this.form.controls.senderAccount.valid),
          map((a) => a.iban),
          startWith('')
        ),
      ])
        .pipe(
          map(([payerIban, senderAccount]) =>
            this.paymentsService.checkPaymentTypeFromIban(
              senderAccount,
              'KZ' + payerIban
            )
          )
        )
        .subscribe((t) => (this.paymentType = t))
    );

    this.applyCounterAgentVerification();
    this.applyContractApplyableRule();
    this.applyContractValidationRule();
  }

  openSavedPayments() {
    this.modalService
      .open(RecipientsDictionaryComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_max-height',
        centered: true,
      })
      .result.then(
        ({
          recipient,
          account,
        }: {
          recipient: Recipient;
          account: RecipientAccount;
        }) => {
          this.form.patchValue({
            bankAccount: account.iban,
            iinOrBin: recipient.iinOrBin,
            name: recipient.name,
            kbeCode: recipient.kbe.code,
          });
          this.form.markAsDirty();
        }
      )
      .catch(() => {
        /*  no selection or dismiss */
      });
  }

  onEmailOnSuccessFocus(event: FocusEvent) {
    this.focusEmail$.next((event.target as HTMLInputElement).value);
  }

  openContractsAddingModal(event: Event = null) {
    event?.preventDefault();
    this.contractsModalsService.showAddContractModal(false);
  }

  showContractDetails(event: Event) {
    event.preventDefault();
    event.stopPropagation();

    this.contractDetails.show();
  }

  retrieveContracts() {
    this.subs.add(
      this.api
        .getFxContracts()
        .pipe(
          finalize(() => {
            this.isFxContractsLoading = false;
          }),
          map((contracts) =>
            contracts.filter((contract) =>
              contract.permittedCurrencies.includes(CURRENCY.KZT)
            )
          )
        )
        .subscribe((contracts) => {
          this.fxContracts = contracts;
        })
    );
  }

  async onContractSelect(selected: IFxContract) {
    const { contract, contractId } = this.form.controls;

    const loaded = await this.currencyPaymentService.getContractById(
      selected.id
    );

    Object.assign(selected, loaded);

    contract.setValue(selected);
    contractId.setValue(selected.id);

    let recipientToSelect = selected.recipients?.find(
      (recipient) => recipient.primary
    );

    const { fxContractRecipient, fxContractRecipientId } =
      this.form.getRawValue();

    if (!fxContractRecipient && !fxContractRecipientId && recipientToSelect) {
      this.selectContractRecipient(recipientToSelect);
      return;
    }

    const foundRecipient = selected.recipients?.find(
      (recipient) =>
        fxContractRecipient?.id === recipient.id ||
        fxContractRecipientId === recipient.id
    );

    if (foundRecipient) {
      recipientToSelect = foundRecipient;
    }

    this.selectContractRecipient(recipientToSelect);
  }

  selectContractRecipient(recipient: IFxContractRecipient) {
    if (!recipient) {
      return;
    }

    const {
      // name,
      // bankAccount,
      fxContractRecipient,
      fxContractRecipientId,
    } = this.form.controls;

    fxContractRecipient.setValue(recipient);
    fxContractRecipientId.setValue(recipient.id);

    // name.setValue(recipient.name || '');

    // if (recipient.iban) {
    // bankAccount.setValue(recipient.iban);
    // }
  }

  public openKnpDictionary(select: JSelectComponent, event: Event) {
    select.close();
    select.blur();
    setTimeout(() => {
      const modalRef = this.modals.open(KnpDictionaryComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_max-height',
        centered: true,
      });

      modalRef.componentInstance.paymentType =
        PAYMENT_TYPE.REGULAR_PAYMENT_ORDER;
      modalRef.result
        .then((item: KNP) => {
          this.form.controls.knp.patchValue(item.code);
          // trigger cd to update dynamic bottom form field slot
          // this.form.controls.purpose.patchValue(
          //   this.form.controls.purpose.value
          // );
        })
        .catch(() => {
          /*  no selection or dismiss */
        });
    }, 0);
  }

  private applyContractApplyableRule() {
    this.isContractApplyable$ = this.controlsStream.kbeCode.pipe(
      map((kbe) => {
        return kbe && kbe[0] !== this.companyService.currentCompany.kod[0];
      })
    );
  }

  private applyContractValidationRule() {
    this.subs.add(
      this.controlsStream.withinContract.subscribe((withinContract) => {
        if (withinContract) {
          this.form.controls.contract.setValidators(Validators.required);
        } else {
          this.form.controls.contract.clearValidators();
        }

        this.form.controls.contract.updateValueAndValidity();
      })
    );
  }

  private applyCounterAgentVerification() {
    const { iinOrBin } = this.form.controls;
    this.subs.add(
      this.controlsStream.iinOrBin
        .pipe(
          distinctUntilChanged(),
          debounceTime(300),
          switchMap((bin) => {
            this.counterParty$.next(null);
            this.iinIsFound$.next(null);
            this.iinIsSubmitted$.next(null);
            if (iinOrBin.valid) {
              this.iinIsNotOwn = this.user.iin !== bin;
              this.isCounterPartyLoading$.next(bin);
            }
            this.isCounterPartyLoading$.next(null);
            return of(null);
          })
        )
        .subscribe((counterPartyResult) => {
          counterPartyResult && this.checkCounterParty(counterPartyResult);
          this.isCounterPartyLoading$.next(null);
        })
    );
  }

  private checkCounterParty(counterPartyResult: ICounterParty) {
    if (counterPartyResult.companyInfo?.name) {
      this.form.controls.name.setValue(counterPartyResult.companyInfo?.name);
    }
    if (counterPartyResult.riskGroups) {
      this.riskGroups = counterPartyResult.riskGroups;
    }

    this.counterParty$.next(
      counterPartyResult.riskGroups?.length === 0
        ? this.counterPartyStatus.SUCCESS
        : this.counterPartyStatus.ERROR
    );
  }

  private listenEmailSearch() {
    const { emailOnSuccess } = this.form.controls;
    this.subs.add(
      this.controlsStream.notify
        .pipe(debounceTime(100))
        .subscribe((v) =>
          v ? emailOnSuccess.enable() : emailOnSuccess.disable()
        )
    );
    this.subs.add(
      this.controlsStream.iinOrBin
        .pipe(
          debounceTime(200),
          filter((iinOrBin) => this.form.controls.iinOrBin.valid),
          switchMap((iinOrBin) => {
            this.fetchingEmailsFor$.next(iinOrBin);
            this.prevEmails$.next([]);
            return this.recipientsService
              .getPrevNotifications(iinOrBin)
              .catch((e) => []);
          })
        )
        .subscribe((emails) => {
          this.prevEmails$.next(emails);
          this.fetchingEmailsFor$.next(null);
        })
    );
    this.search = (text$: Observable<string>) => {
      return combineLatest([
        this.prevEmails$.pipe(
          map((emails) => emails.map((emailEntity) => emailEntity.email))
        ),
        text$.pipe(debounceTime(200), distinctUntilChanged(), startWith('')),
        this.focusEmail$,
      ]).pipe(
        map(([emails, term, focus]) => {
          if (term) {
            return emails
              .filter(
                (item) => item.toLowerCase().indexOf(term.toLowerCase()) > -1
              )
              .slice(0, 3);
          } else {
            return emails.slice(0, 3);
          }
        })
      );
    };
  }

  async checkIin() {
    this.riskGroups = [];
    this.iinIsSubmitted$.next(true);
    const iinOrBin = this.form.controls.iinOrBin.value;
    this.isCounterPartyLoading$.next(iinOrBin);
    this.recipientsService
      .checkCounterParty(iinOrBin)
      .then(async (counterPartyResult) => {
        counterPartyResult && this.checkCounterParty(counterPartyResult);
        await this.successfulCounterPartyResult();
      })
      .catch((e) => {
        console.error(e);
        this.iinIsSubmitted$.next(null);
        this.iinIsFound$.next(null);
        this.isCounterPartyLoading$.next(null);
        this.showSignError();
      });
  }
  private async showSignError() {
    return this.modalService.open(IinCheckErrorComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_max-height',
      centered: true,
    }).result;
  }
  async successfulCounterPartyResult() {
    this.iinIsSubmitted$.next(null);
    this.iinIsFound$.next(true);
    this.isCounterPartyLoading$.next(null);
  }
}
